.projects__main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}
.projects__main .my__projects {
  height: 68vh;
  overflow-y: scroll;
}

.project__card__container {
  position: absolute;
  z-index: 10;
  display: grid;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  padding: 0.5rem;
}

.hazy {
  filter: blur(5px);
}

.project__summary {
  border: none;
}

@media only screen and (min-width: 600px) {
  .project__summary {
    width: 40%;
  }
}
@media only screen and (min-width: 1200px) {
  .project__summary {
    width: 22%;
  }
}