@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Yellowtail&display=swap);
body {
  margin: 0;
  padding:0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/myportfolio/static/media/main-bg.78a540b7.jpg);
  background-repeat:repeat;
  background-size: cover;
  min-height:100vh ;
  width:100%;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.cursor-pointer{
  cursor: pointer;
}
:root {
  --animate-delay: 0.25s;
}
.titles{
 font-family: 'Yellowtail', cursive;
  font-size: 2.5rem;
  color: azure;
  text-align: center;
}

.notFound__main {
  margin: 0 auto;
  width: 100;
  height: 100vh;
  display: grid;
  align-items: center;
}
.notFound__main h1, .notFound__main h2 {
  text-align: center;
}
.notFound__main h1 {
  color: red;
  font-weight: 700;
}
.footer {
  width: 100%;
  background-color: #353A3E;
  color: #f5f2e0;
  font-size: 1rem;
}
.footer ul {
  list-style: none;
}
.footer .icons {
  font-size: 1.7rem;
}
.footer .icons:hover {
  color: #e4be17;
}
.footer .links:hover a {
  color: #e4be17;
}
.footer .links:hover .icons {
  color: #e4be17;
}
.footer p {
  margin: 0 2rem;
}
.footer p:hover {
  color: #e4be17;
}

a {
  text-decoration: none;
  color: #f5f2e0;
  margin: 10px 10px;
}
a:hover {
  color: #e4be17;
}
.header {
  background-color: #353A3E;
}
.header .burger__menu {
  color: #f5f2e0;
  display: none;
  margin: 0;
  padding: 0;
}
.header .navbar {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 2;
  background-color: #353A3E;
  justify-content: center;
}
.header .navbar li {
  list-style: none;
}
.header .navbar li .react__link {
  text-decoration: none;
  color: #f5f2e0;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 1rem;
}
.header .navbar li .active {
  color: #e4be17;
}
.header .navbar li .react__link:hover {
  color: #bb9c47;
}

@media only screen and (max-width: 600px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    top: 4rem !important;
  }

  .burger__menu {
    color: white;
    font-size: 3rem;
    display: block !important;
  }
}
.project__brief__item {
  box-shadow: 3px 3px 5px 6px #b1daf5;
  border-radius: 0.5rem;
}
.project__brief__item img {
  width: 100%;
  height: 240px;
  margin-bottom: 1.5rem;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}
.project__brief__item .click__note {
  text-align: center;
  color: red;
  font-weight: 700;
  font-style: italic;
}
.close-display {
  text-align: right;
  cursor: pointer;
  padding: 0;
  margin: 0;
}

.project__card__main img {
  display: block;
  width: 100%;
}

.tech__item {
  background-color: #fc5353;
  color: white;
  font-weight: 500;
  padding: 0 5px;
  border-radius: 10px;
}
.projects__main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}
.projects__main .my__projects {
  height: 68vh;
  overflow-y: scroll;
}

.project__card__container {
  position: absolute;
  z-index: 10;
  display: grid;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  padding: 0.5rem;
}

.hazy {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.project__summary {
  border: none;
}

@media only screen and (min-width: 600px) {
  .project__summary {
    width: 40%;
  }
}
@media only screen and (min-width: 1200px) {
  .project__summary {
    width: 22%;
  }
}
.home__main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}
.home__main .my__name {
  margin: 0;
  padding: 0;
  color: orange;
  font-size: 4rem;
  font-family: "Permanent Marker", cursive;
}
.home__main .my__photo {
  border-radius: 15rem;
  height: 20rem;
  display: block;
  margin: 0;
}
.home__main .main__content__container {
  height: 66vh;
  overflow-y: scroll;
}
.contact__main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}
.contact__main .main__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 66vh;
  overflow-y: scroll;
}
